import client from '@mailchimp/mailchimp_marketing';
import * as regexes from './regexes';

client.setConfig({
	apiKey: 'a1d1071b5a90018494a4a86420a4bd55-us2',
	server: 'us2',
});

export async function subscribe(data: {
	name: string
	email: string
}) {
	let names = data.name.split(regexes.space);
	let FNAME = names.shift() as string;
	let LNAME = names.join(' ');
	await client.lists.addListMember('968424', {
		email_address: data.email,
		status: 'transactional',
		mergeFields: {
			FNAME, LNAME
		}
	});
}